.logo-text {
  font-weight: bold;
  position: absolute;
}

.logo-primary {
  font-size: 1.5em;
  padding: 0.75em;
  top: 15%;
}

.logo-title {
  font-size: 1em;
  margin: 0.5em 0;
  padding: 0 1em;
  top: 2%;
}

@media only screen and (min-width: 720px) and (max-width: 960px) {

  .logo-primary {
    font-size: 2vw;
  }
  
  .logo-title {
    font-size: 1.5vw;
  }

}

@media only screen and (min-width: 540px) and (max-width: 720px) {

  .logo-primary {
    font-size: 2.5vw;
  }
  
  .logo-title {
    font-size: 2vw;
  }

}

@media only screen and (min-width: 480px) and (max-width: 540px) {

  .logo-primary {
    font-size: 2vw;
  }
  
  .logo-title {
    font-size: 1.5vw;
  }

}